@import 'variables';


.header {
    height: 80px;
    background-color: $secondary-color;
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-width: 100%;
    position: sticky;
    top: 0px;
    z-index: 1;
    &__logo {
        padding: 15px 35px;
        max-height: 60px; }
    &__nav {
        display: flex;
        justify-content: space-between;
        align-items: center;
        .active {
            color: #FFCB03; }
        &__a {
            text-decoration: none;
            font-family: 'main';
            font-size: 14px;
            color: #FFFFFF;
            text-transform: uppercase;
            margin-right: 32px;
            &:last-child {
                padding-right: 36px; }
            &:hover {
                color: $main-color;
                text-decoration: none;
                border-top: 5px solid $main-color;
                height: 46px;
                padding-top: 29px; } }
        &__button {
            @include button;
            margin-right: 35px; } } }




@media screen and (max-width: 1168px) {
    .header {
        &__nav {
            &__a {
                font-size: 0.65rem; } } } }


.header__nav__dropdown {
  position: relative;
  margin-bottom: 3px;

  &.active {
    .header__nav__dropdown-content {
      display: block; } }

  .header__nav__dropdown-content {
    display: none;
    position: absolute;
    top: 100%;
    left: 0;
    background-color: #23202088;
    padding: 10px;
    z-index: 1;
    border-radius: 15px;

    a {
      display: block;
      font-family: 'main';
      padding: 15px 25px;
      text-decoration: none;
      color: $main-color;
      border-radius: 15px;
      &:first-child {
        margin-top: 20px; }

      &:hover {
        background-color: $secondary-color; }

      &.active {
        background-color: $secondary-color; } } } }



